:root {
    --primary-color: #00ff00;
    --secondary-color: rgba(0, 255, 0, 0.7);
    --background-color: #0a0a0a;
    --text-color: #ffffff;
    --border-color: #00ff00;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Arial', sans-serif; /* Default system font */
}

.auth-container {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--background-color);
    background-size: cover;
    background-position: center;
}

.auth-card {
    background: rgba(0, 0, 0, 0.9);
    border-radius: 10px;
    padding: 40px;
    text-align: center;
    box-shadow: 0 0 20px var(--secondary-color);
    border: 2px solid var(--border-color);
    max-width: 400px;
    width: 90%;
    animation: fadeIn 0.5s ease-in-out;
}

.auth-title {
    color: var(--primary-color);
    font-size: 24px;
    margin-bottom: 20px;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.auth-input {
    width: 100%;
    padding: 12px;
    margin: 10px 0;
    background: transparent;
    border: 2px solid var(--border-color);
    color: var(--primary-color);
    font-size: 16px;
    text-align: center;
    border-radius: 5px;
    outline: none;
    transition: 0.3s ease;
}

.auth-input::placeholder {
    color: rgba(0, 255, 0, 0.5);
    text-transform: uppercase;
}

.auth-input:focus {
    border-color: var(--secondary-color);
    box-shadow: 0 0 10px var(--secondary-color);
}

.auth-button {
    width: 100%;
    padding: 12px;
    margin-top: 15px;
    background: var(--primary-color);
    color: black;
    font-weight: bold;
    font-size: 16px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    text-transform: uppercase;
    transition: 0.3s ease;
}

.auth-button:hover {
    background: #008f00;
    box-shadow: 0 0 10px var(--secondary-color);
}

.auth-error {
    color: red;
    margin-top: 10px;
    font-weight: bold;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
